export default {
		data() {
			return {
				globalData:{},
				formData:{
					type:"",
					name:"",
					price:"",
					num:"",
					more:'',
					imgs:[]
				},
				modalName: null,
				textareaAValue: '',
				textareaBValue: ''
			};
		},
		onLoad() {
			this.globalData = getApp().globalData
		},
		methods: {
			OnSubmit() {
				let formData = JSON.parse(JSON.stringify(this.formData))
				formData.imgs = formData.imgs.join(',')
				let errMsg = '';
				if(!formData.type){
					errMsg = "请输入产品类型"
				}else if(!formData.name){
					errMsg = "请输入产品名称"
				}else if(!formData.price){
					errMsg = "请输入期望价格"
				}else if(!formData.num){
					errMsg = "请输入期望数量"
				}
				if(errMsg){
					uni.showToast({
						icon: 'none',
						title: errMsg,
						duration: 2000
					});
					return false;
				}
				this.$minApi
					.goodsSearchAdd(formData)
					.then(res => {
						uni.showToast({
							title: "提交成功",
							duration: 2000
						});
						setTimeout(function(){
							uni.navigateBack({
							    delta: 1
							});
						},2000)
					})
					.catch(err => {
						console.log(err);
					});
			},
			ChooseImage() {
				uni.chooseImage({
					count: 4,
					sizeType: ['original', 'compressed'],
					sourceType: ['album'],
					success: (res) => {
						const tempFilePaths = res.tempFilePaths;
						uni.uploadFile({
							url: this.globalData.api_url+'/upload',
							filePath: tempFilePaths[0],
							name: 'file',
							formData: {},
							success: (uploadFileRes) => {
								this.formData.imgs.push(uploadFileRes.data)
							}
						});
						
					}
				});
			},
			ViewImage(e) {
				uni.previewImage({
					urls: this.formData.imgs,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				this.formData.imgs.splice(e.currentTarget.dataset.index, 1)
			},
			InputType(e) {
				this.formData.type = e.detail.value
			},
			InputName(e) {
				this.formData.name = e.detail.value
			},
			InputPrice(e) {
				this.formData.price = e.detail.value
			},
			InputNum(e) {
				this.formData.num = e.detail.value
			},
			InputMore(e) {
				this.formData.more = e.detail.value
			},
		}
	}
